/* * {
    box-sizing: border-box;
} */

.alta {
    min-height: 600px;
    padding: 20px;
    background-color: #ddd;
}

.alta h1 {
    text-align: center;
}

.alta .alta-form {
    background-color: #b63625;
    width: 80%;
    margin: 20px auto;
    padding: 30px;
    color: white;
    border-radius: 20px;
    border: 1px dashed yellow;
}

.alta .input-group {
    text-align: center;
    padding: 3px;
    /* border: 1px solid cyan; */
}

.alta label {
    color: #badc58;
    text-transform: capitalize;
    display: block;
    margin-bottom: 5px;
}

.alta label[for="envio"] {
    display: inline-block;
    margin: 10px 0;
}

.alta button {
    /* text-align: center; */
    display: block;
    margin: 15px auto;

    height: 40px;
    width: 40%;
    border-radius: 5px;
    font-size: 20px;
}

.alta .btnAgregar {
    background-color: #6ab04c;
    color: #130f40;
}

.alta .btnAgregar:disabled {
    opacity: 0.5;
}

.alta .btnActualizar {
    background-color: gold;
    color: #222;
}

.alta .btnActualizar:disabled {
    opacity: 0.5;
}

.alta input {
    width: 70%;
    height: 30px;
    border-radius: 5px;
    background-color:#95afc0;
}

.alta input[type="checkbox"] {
    width: 20px;
    height: 20px;
}

/* ------------------------------ */
.alta th, .alta td {
    border: 1px solid #999;
    width: 80px;
    height: 30px;
    padding: 10px;
}

.alta th {
    text-transform: uppercase;    
    background-color: #111;
    color: white;
    /* border: 1px solid white; */
}

.alta table {
    border-collapse: collapse;
    margin: 5px auto;
    /* border: 1px solid white; */
}

.alta h2 {
    text-align: center;
}

/* ------------------ */
.alta table .borrar-editar {
    width: 90%;
    height: 40px;
    border-radius: 5px;
    font-size: 18px;
}

.alta table button[id^="btnBorrar-"] {
    background-color: crimson;
    color: white;
}

.alta table button[id^="btnBorrar-"]:disabled {
    opacity: 0.5;
}

.alta table button[id^="btnEditar-"] {
    background-color: gold;
    color: #222;
}

.alta table button[id^="btnCancelar-"] {
    background-color: #222;
    color: gold;
    font-size: 16px;
}

.alta table .centrar {
    text-align: center;
}