body, ul, h3, h1, p {
    margin: 0;
}

body {
    font-family: "Open Sans", sans-serif;
}

header {
    /* border: 1px solid magenta; */
    width: 100%;
    height: auto;
}

header nav {
    background-color: #48D1CC;
    width: 100%;
    height: auto;

    display: flex;
    justify-content: center;
    /* align-items: center; */
}

header nav ul {
    /* border: 1px solid blue; */
    width: 80%;
    height: auto;

    list-style: none;
    padding: 0;

    display: flex;
    /* flex-wrap: nowrap; */
    /* justify-content: space-evenly; */

    /* flex-direction: row; */
    /* flex-direction: column;
    flex-direction: row-reverse;
    flex-direction: column-reverse; */
}

header nav ul li {
    /* border: 1px solid yellow; */
    width: 100%;
    height: auto;
}

header nav ul li a {
    text-decoration: none;    
    /* border: 1px solid white; */
    width: 115px;
    height: 54px;
    display: block;
    background-color: #800080;
    color: white;
    margin: auto;

    text-align: center;
    line-height: 54px;
}

/* -------------------------- */
header > div {
    background-color: #555;
    width: 100%;
    /* height: 180px; */
    border: 1px solid yellow;

    display: flex;
    /* justify-content: space-around;
    align-items: center; */
}

#logo {
    height: 54px;
    width: 10%;
    background-color: #FFFF00;
    text-align: center;
    line-height: 54px;
    font-size: 22px;
}

#barra-busqueda {
    height: 54px;
    width: 85%;
    background-color: #FFD700;
    /* text-align: center;
    line-height: 54px; */
    display: flex;
    justify-content: center;
    align-items: center;
}

#barra-busqueda form {
    display: flex;
    justify-content: center;
    align-items: center;
    /* border: 1px solid cyan; */
    width: 100%;
}

#boton-carrito {
    height: 54px;
    width: 5%;
    background-color: #DAA520;
    text-align: center;
    line-height: 54px;
    font-size: 20px;
}

#barra-busqueda form label {
    background-color: #ADFF2F;
    width: 100px;
    height: 22px;
    text-align: center;
    line-height: 22px;
}

#barra-busqueda form input {
    background-color: #FF1493;
    padding: 2px;
    width: 50%;
    color: white;
    height: 20px;
    margin: 0 2px 0 5px;    
}

#barra-busqueda form input[type="submit"] {
    background-color: #2F4F4F;
    padding: 2px;
    width: 100px;
    height: 28px;
    margin: 0; 
}

/* --------------------------- */
main {
    /* height: 550px; */
}

/* --------------------------- */
footer {
    background-color: blueviolet;
    color: white;
    height: 100px;
    text-align: center;
    line-height: 100px;
    font-size: 22px;
}
